.bg-greyish {
    .bg-grey {
        position: absolute;
        // display: block;
        font-size: 0;
        width: 100%;
        height: 39px;
        top: 0%;
        right: 0%;
        background: #F5F5FA;
        z-index: 0;
    }
    
    @media screen and (min-width: 768px) {
        .bg-grey {
            display: none;
        }
    
    }
}