* {
    box-sizing: border-box;
    margin: 0;
    
}

.body {
    font-family: 'Montserrat', 'Verdana', sans-serif;
    font-weight: 400;
    font-style: normal;
    margin: 0;
}

.container {
    max-width: 1350px;
    min-width: 375px;
    margin: 0 auto;
    padding: 0 30px;
}

.container-header {
    max-width: 1350px;
    min-width: 375px;
    margin: 0 auto;
    padding: 0 15px;
}

.visually-hidden {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    margin: -1px;
}

.img-responsive {
      max-width: 100%;
      height: auto;
}

a {
      color: #ffff;
      text-decoration: none;
      &:hover {
        text-decoration: none;
       }
  }


.bg-letter {
    background-image: url(../img/letter.svg);
    background-position: top center;
    background-repeat: no-repeat;
    background-size: auto 100%;
}

input, textarea, select, button {
    font: inherit;
  }

@media screen and (min-width: 768px) { 
    .container {
        padding: 0 60px;
    }
    .container-header {

        padding: 0 60px;
    }
}

@media screen and (min-width: 1201px) {
    .container {
        padding: 0 15px;
    }
    .container-header {

        padding: 0 15px;
    }
}
